import React from "react";

const AddNew = (props) => {
  return (
    <>
      <form onSubmit={(e) => props.addPerson(e)}>
        <div>
          name:
          <input
            type="text"
            onChange={(e) => props.handleSetName(e)}
            required
          />
        </div>
        <div>
          number:
          <input
            type="text"
            onChange={(e) => props.handleSetPhone(e)}
            required
          />
        </div>
        <div>
          <button type="submit">add</button>
        </div>
      </form>
    </>
  );
};

export default AddNew;
